@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600&family=Roboto:wght@100;300;400;500&display=swap);
.home{min-height:100vh;width:100%;display:flex;align-items:center;justify-content:center}.home .hero{width:60%;height:200px;display:flex;justify-content:center;align-items:center;flex-direction:column;margin-bottom:200px}.home .hero h2{font-weight:100;font-size:38px;letter-spacing:5px}.home .hero h2 span{font-weight:300}.home .hero a{margin-top:40px;padding:10px 20px;border:2px solid rgba(255,255,255,0.801);font-weight:200;transition:0.4s all ease}.home .hero a:hover{background-color:#4075e5;border:2px solid #4075e5;border-radius:5px}

.shop{width:90%;margin:50px auto}.shop .item-container{display:flex;flex-wrap:wrap;margin:0 auto;justify-content:center}.shop .product-card{width:240px;min-height:420px;margin:30px;padding:10px;border-radius:10px;background-color:rgba(0,0,0,0.815);display:flex;flex-direction:column;justify-content:space-around;align-items:center}.shop .product-card .item-title{width:100%;text-align:center;margin:10px auto;font-weight:300;padding:10px 0;border-bottom:1px solid rgba(211,211,211,0.205)}.shop .product-card img{height:300px;width:95%}.shop .product-card div{width:100%;display:flex;justify-content:space-around;align-items:center}.shop .product-card div button{background-color:#4075e5;border:none;color:white;padding:10px;border-radius:5px;cursor:pointer;transition:0.3s all ease}.shop .product-card div button:hover{transform:scale(1.05)}.shop .product-card div .item-price{font-size:20px;font-weight:300}

.cart{width:90%;margin:100px auto;background-color:rgba(0,0,0,0.8);min-height:400px;padding:10px;display:flex;flex-direction:column}.cart h2{font-weight:100;font-size:40px;padding:5px;border-bottom:1px solid rgba(211,211,211,0.212)}.cart .cart-item{display:flex;align-items:center;padding:10px;border-bottom:1px solid rgba(211,211,211,0.212);position:relative}.cart .cart-item .item-image{height:72px}.cart .cart-item .item-title{margin-left:30px}.cart .cart-item .total-price{margin-left:auto}.cart .cart-item .quantity-wrapper{position:absolute;right:25%;display:flex;width:80px}.cart .cart-item .quantity-wrapper>*{flex:1 1;text-align:center}.cart .cart-item .quantity-wrapper button{font-family:inherit;background-color:#4075e5;color:white;border:none;border-radius:2px;font-size:18px;font-weight:100}.cart .cart-item .trash{margin:0 20px;cursor:pointer}.cart .general-total{margin-top:auto;padding:20px;border-top:1px solid rgba(211,211,211,0.212);display:flex;align-items:center;justify-content:space-between}.cart .general-total span{font-size:17px}.cart .general-total span #amount{margin-left:10px;font-size:23px;font-weight:200}.cart .general-total button{padding:10px;background-color:green;color:white;border:none;border-radius:5px;cursor:not-allowed}

*{padding:0;margin:0;box-sizing:border-box}body{background-color:#000;color:white;font-family:"Roboto", sans-serif}a{color:inherit;text-decoration:none}.App{background:linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)),url(/shopping-cart/static/media/hero.104870b4.jpg);min-height:120vh;width:100%;background-size:cover}.App>*:not(nav){animation:appear 0.7s forwards}nav{height:60px;display:flex;padding:30px 40px 0 40px;align-items:center;justify-content:space-between}nav .logo{display:flex;align-items:center;margin-left:5%}nav .logo h1{font-weight:500;margin-left:10px}nav .logo span{font-weight:100}nav .logo img{height:40px}nav .nav-links{display:flex;justify-content:space-around;align-items:center;width:25%;color:rgba(255,255,255,0.699);margin-right:10%}nav .nav-links a{font-weight:300;font-size:17px;transition:0.4s all ease}nav .nav-links a:hover{color:white}nav .nav-links #cart-icon{position:relative}nav .nav-links #cart-icon span{display:flex;align-items:center;justify-content:center;position:absolute;width:18px;height:18px;border-radius:50px 50px 50px 10px;background-color:#4075e5;color:white;bottom:95%;left:100%;font-size:15px;font-weight:400}@keyframes appear{from{opacity:0;transform:translateX(-50px)}to{opacity:1;transform:translateX(0)}}

